import Vue from 'vue'
import VueRouter from 'vue-router';

Vue.use(VueRouter);//    :Vue.component('router-link',routerLink),

import {setCookie,getCookie,delCookie} from '../utils/utils';


import routes from './router.config'

let router = new VueRouter(routes);


// 
router.beforeEach((to, from, next) => {

    const nextRoute = ['home', 'banner', 'fodder', 'taoxi', 'cases', 'otherCookie', 'shop', 'intro', 'team', 'cookie', 'contactUs', 'city', 'links','copeRight', 'erCode', 'icp'] // 
    let isLogin = getCookie('username')  // 
    //  nextRoute  UserLogIn
    if (nextRoute.indexOf(to.name) >= 0) { // 
      if (!isLogin) { // 
        if (from.name === 'login') {
          next('/')
          return
        }
  // 
        router.push({
          name: 'login',
          params: {redirect: to.fullPath}  
        //   params: {redirect: to.fullPath} 
         
        })
      }
    }
    //  UserLogIn  Home
    if (to.name === 'login') {
      if (isLogin) {
        next('/home')
        return
      }
    }
    next() //  next , next 
  })
export default router


